.homepage {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 4rem 5vw;
  height: 100%;
}

.nameCard {
  max-width: 50%;
  z-index: 1;
}

.profilePic  {
  width: clamp(200px, 50vw, 500px);  /* 📱 < 🖥️ Smooth scaling */
  height: auto;
}

.hey,
.wendy,
.title{
  margin: 0;
  padding: 0;
  line-height: 1.5;
}

.hey {
  font-weight: 100;
  font-size: clamp(16px, 3vw, 25px);
}

.wendy {
  font-weight: 800;
  font-size: clamp(36px, 8vw, 80px);
  color: #1E82A6;
}
.title {
  font-weight: 400;
  font-size: clamp(24px, 5vw, 50px);
}